import React, {useState, useEffect} from 'react';
import CollectData from "./CollectData"
import { MyCarousel } from './MyCarousel';  
import { MyParallax } from './MyParallax';
import { FullscreenBanner } from './FullscreenBanner';
import { InfoPanel } from './InfoPanel';
import { SphereView } from './SphereView';
import { Helper } from '../Helper';
import Preload from 'react-preload'
import csvToJSON from "./csvToJSON";

export const Home=()=>{

    const [csvData,setcsvData]=useState([])
    const[loaded,setLoaded]=useState(false)
    useEffect(()=>{
        if(!loaded){
            const url="https://raw.githubusercontent.com/kmagdi/KSZC-Data/master/reszletekKK.csv"
            fetch(url)           
                .then(resp=>resp.text())
                .then(text=>{
                    const adatokJSON=csvToJSON(text, ';')
                    //console.log(adatokJSON)
                    const filtered=adatokJSON.filter(obj=>obj.kod!==undefined&&obj.kod!=='0'&&obj.kod!=="")
                    setcsvData(filtered)
                },[])
            setLoaded(true)
        }
    })

    const images = {
        head: Helper.getMODI(require('./assets/eszt.jpg'),require('./assets/eszt_mobile.jpg')),
        other: Helper.getMODI(require('./assets/eszt2.jpg'),require('./assets/eszt2_mobile.jpg')),
        logo: require('./assets/logo.png')
    };
    //console.log(Object.values(images).concat(csvData.map((i)=>('./assets/' + i.kod + '.jpg'))));
    let carouselImages = csvData.map((i)=>([Helper.getMODI(require('./assets/' + i.kod + '.jpg'),require('./assets/' + i.kod + '_mobile.jpg')),i]));
    return(
        <Preload
            loadingIndicator={(
                <div style={{width:'100vw',height:'100vh',backgroundColor:'#343a40',color:'white'}}>
                    <div className="kozep">
                        {/* <h1>Kpek betltse</h1> */}
                        <img src={require('./assets/loading.gif')} alt={"logo"} />
                    </div>
                </div>
            )}
            images={Object.values(images).concat(carouselImages.map((i)=>(i[0])))}
            autoResolveDelay={null}
        >
            <>
                <FullscreenBanner id="home" bgimage={images.head} image={images.logo} title={'UNDER CONSTRUCTION '+(Helper.isMobile()?'(Mobil verzi)':'(Asztali verzi)')} />

                <InfoPanel id="iskola" size={6} type="centerInfo" title="Iskolnkrl..." text="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus risus diam, euismod non orci ut, ornare varius eros. Cras pellentesque, sapien in consequat accumsan, nunc quam pulvinar nulla, sodales rhoncus diam mi vitae tellus. Nunc vulputate ligula nisl, id dignissim turpis vulputate sed. Aliquam tincidunt porttitor odio sed vulputate. Aliquam sit amet maximus mi, a fringilla urna. Ut quis sem a mauris facilisis rhoncus. Fusce malesuada arcu est, volutpat pellentesque est tincidunt vel. Quisque porta erat orci, vel efficitur sem cursus id. Curabitur dignissim lorem in dui aliquam, in cursus metus volutpat. Suspendisse porttitor augue mollis nibh dignissim, vel suscipit tortor placerat. Nulla faucibus, justo et ornare molestie, tortor purus faucibus lectus, at efficitur erat ex et mauris. In congue diam non risus suscipit ultrices." />

                <MyParallax img={images.other} amount={0.25} height={'40vh'} sizeToContent={true}>
                    <InfoPanel id="par1" bold={true} type="centerInfo" bgColor={"rgba(255,255,255,0.5)"} nomargin={true} height={'40vh'} text='"Az iskola arra val, hogy az ember megtanuljon tanulni, hogy felbredjen tudsvgya, megismerje a jl vgzett munka rmt, megzlelje az alkots izgalmt, s megtallja a munkt, amit szeretni fog." (Szentgyrgyi Albert) [DESIGN MG NEM KSZ]' from={{opacity:0,scaleX:0.5,scaleY:0.5,ease:'power4.out'}} to={{opacity:1,scaleX:1.0,scaleY:1.0,ease:'power4.out'}} />
                </MyParallax>

                <InfoPanel id="else" className="sidebyside-margins" type="sideBySide" title="Something else" text="Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Ne egybl parallax utn kvetkezzenek a szakok." image={images.head} from={{opacity:0,x:200,ease:'power4.out'}} to={{opacity:1,x:0,ease:'power4.out',stagger:{amount:0.2}}} />

                <MyParallax img={images.other} amount={0.25} height={'40vh'} sizeToContent={true}>
                    <InfoPanel id="par2" bold={true} type="centerInfo" bgColor={"rgba(255,255,255,0.5)"} nomargin={true} height={'40vh'} text='Egyiknk lete sem knny. s akkor? Legyen bennnk kitarts, s mindenekeltt bzzunk nmagunkban. Hinnnk kell benne, hogy tehetsgesek vagyunk valamiben, s ezt a valamit -kerl, amibe kerl- meg tudjuk valstani. (Marie Curie) [DESIGN MG NEM KSZ]' from={{opacity:0,scaleX:0.5,scaleY:0.5,ease:'power4.out'}} to={{opacity:1,scaleX:1.0,scaleY:1.0,ease:'power4.out'}} />
                </MyParallax>

                <InfoPanel id="else2" className="sidebyside-margins" type="sideBySide" title="Something else" text="Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Valami ms is jhetne ide, csak hogy ne egybl parallax utn kvetkezzenek a szakok. Ne egybl parallax utn kvetkezzenek a szakok." reverse={true} image={images.head} from={{opacity:0,x:-200,ease:'power4.out',stagger:{amount:0.2}}} to={{opacity:1,x:0,ease:'power4.out'}} />

                <div id="szakok" className="row justify-content-center">
                    <MyCarousel kepek={carouselImages} />
                </div>

                <div className={'panorama-container-container'}>
                    <SphereView id="1" image={Helper.getMODI(require('./assets/pano1.jpg'),require('./assets/pano1.jpg'))} />
                    <SphereView id="2" image={Helper.getMODI(require('./assets/pano2.jpg'),require('./assets/pano2.jpg'))} />
                </div>

                <CollectData bgimage={images.head} />
            </>
        </Preload>

    )
}