import React from 'react';
import {Row,Col} from 'react-bootstrap';
import './Footer.css';
import { MyParallax } from './MyParallax';

export const Footer=(props)=> {
    return(
        <div>
            <div id="pre-footer">
                {/*<p>Taln valami szveg a footer eltt</p>*/}
                <MyParallax img={props.bgimage} amount={0.5} sizeToContent={true}>
                    <img className="foot-fade" src={require('./assets/fade.png')} alt="Footer eltti kp mgtti kp" />
                    <img src={require('./assets/footer.png')} alt="Footer eltti kp" />
                </MyParallax>
            </div>
            <Row id="footer">
                <Col lg={2}></Col>
                <Col lg={4} className="footer-col footer-col-first">
                    <p><b>F Kand weboldal:</b><br/><a href="http://www.kkando.hu/" target="_blank" rel="noopener noreferrer">http://www.kkando.hu/</a></p>
                    <p><b>Szkhely:</b><br/>{props.contactData.location}</p>
                    <p><b>Levelezsi cm:</b><br/>{props.contactData.adress}</p>
                    <p><b>Telefon:</b><br/>{props.contactData.tel}</p>
                    <p><b>E-mail:</b><br/>{props.contactData.email}</p>
                    <p><b>OM azonost:</b><br/>{props.contactData.omid}</p>        
                </Col>
                <Col lg={4} className="footer-col footer-col-second">
                    <p style={{marginBottom:'0px'}}>Copyright &copy; KSZC Kand 2016-2020. Minden&nbsp;Jog&nbsp;Fenntartva.</p>
                    <p style={{fontSize:'95%'}}>Weboldalt ksztette: Ktay Magdolna, Varga Brigitta, Sata Erik</p>
                    <p></p>
                </Col>
                <Col lg={2}></Col>
            </Row>
        </div>
       )}